<template>
    <main>
        <header class="d-flex align-items-center justify-content-center mb-5">
            <h1>Classroom</h1>
        </header>
        <div class="container-fluid">
            <div class="row">
                <div v-for="device in devices" :key="device.id" class="col-sm-4 mb-4">
                    <device-thumb :device-id="device.id" @toggle-select="toggleSelect($event)"></device-thumb>
                </div>
            </div>
        </div>
    </main>
</template>
<script>
import DeviceThumb from "./DeviceThumb";

export default {
    name: 'DeviceIndex',
    components: {
        DeviceThumb
    },
  props: ['apiBase'],
  data: function () {
    return {
      pollInterval : null,
      pollInFlight : false,
      devices : []
    }
  },
  methods : {
    pollDevices : async function() {
        if(this.pollInFlight === true) {
          return;
        }
        this.pollInFlight = true;
        try {
          const deviceResponse = await fetch(this.apiBase + '/devices', {
              'headers': {
                  'Authorization': 'BADC00FFEE'
              }
          });
          if(deviceResponse && deviceResponse.ok) {
            const payload = await deviceResponse.json();
            let existing = false;
            for(let i=0; i<payload.device_ids.length; i++) {
              existing = false;
              for(let j=0; j<this.devices.length; j++) {
                if(payload.device_ids[i] === this.devices[j].id) {
                  existing = true;
                }
              }
              if(existing === false) {
                this.devices.push({
                  id : payload.device_ids[i],
                  selected : false
                });
              }
            }
            for(let i=0; i<this.devices.length; i++) {
              let remove = true;
              for(let j=0; j<payload.device_ids.length; j++) {
                if(this.devices[i].id === payload.device_ids[j]) {
                  remove = false;
                }
              }
              if(remove === true) {
                this.devices.splice(i, 1);
              }
            }
          }
        }
        catch(e) {
          // ES-Lint Ignore
        }
        this.pollInFlight = false;
      },
    toggleSelect : function(e) {
      for(let i=0; i<this.devices.length; i++) {
        if(this.devices[i].id === e.device_id) {
          this.devices[i].selected = e.selected;
        }
      }
    },
    getSelectedDevices : function() {
      const ret = [];
      for(let i=0; i<this.devices.length; i++) {
        if(this.devices[i].selected === true) {
          ret.push(this.devices[i].id);
        }
      }
      return ret;
    },
  },
  created : function () {
    this.pollInterval = setInterval(this.pollDevices, 5000);
    this.pollDevices();
  },
  beforeUnmount() {
    clearInterval(this.pollInterval);
    this.pollInFlight = false;
  }
}
</script>
