<template>
    <div class="row g-0">
        <MultiDeviceControlPanel
            @send-message="sendMessage($event)"
            @open-website="openWebsite($event)"
            @lock-screen="lockScreen($event)"
            @unlock-screen="unlockScreen($event)"
            @disable-web="disableWeb($event)"
            @enable-web="enableWeb($event)"
            @set-block-list="setBlockList($event)"
            @set-allow-list="setAllowList($event)"
            @clear-block-list="clearBlockList()"
            @clear-allow-list="clearAllowList()"
        />
        <DeviceIndex
            ref="devices"
            :api-base="apiBase"
        />
    </div>
</template>
<script>
import MultiDeviceControlPanel from './components/MultiDeviceControlPanel.vue'
import DeviceIndex from './components/DeviceIndex';

export default {
    name: 'App',
    components: {
        MultiDeviceControlPanel,
        DeviceIndex
    },
    data: function () {
        return {
            apiBase: 'https://5lcpg07jce.execute-api.eu-west-2.amazonaws.com'
        }
    },
    methods: {
        sendMessage: async function (e) {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/message', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'method': 'send_message',
                        'payload': {
                            'title': 'Message from Scalable Classroom',
                            'body': e.message
                        }
                    }
                })
            });
        },
        openWebsite: async function (e) {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/message', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'method': 'open_website',
                        'payload': {
                            'url': e.url
                        }
                    }
                })
            });
        },
        lockScreen: async function () {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'lockScreen': true
                    }
                })
            });
        },
        unlockScreen: async function () {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'lockScreen': false
                    }
                })
            });
        },
        disableWeb: async function () {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'disableWeb': true
                    }
                })
            });
        },
        enableWeb: async function () {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'disableWeb': false
                    }
                })
            });
        },
        setBlockList: async function (e) {
            alert('Set block' + e.sites.join(','));
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'blockList': e.sites
                    }
                })
            });
        },
        clearBlockList: async function () {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'blockList': null
                    }
                })
            });
        },
        setAllowList: async function (e) {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'allowList': e.sites
                    }
                })
            });
        },
        clearAllowList: async function () {
            const device_ids = this.$refs.devices.getSelectedDevices();
            await fetch(this.apiBase + '/twin', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization' : 'BADC00FFEE'
                },
                body: JSON.stringify({
                    'device_ids': device_ids,
                    'payload': {
                        'allowList': null
                    }
                })
            });
        }
    }
}
</script>

<style lang="scss" rel="stylesheet/scss">
#app {
    aside {
        min-height: 100vh;
        flex: 0 0 auto;
        width: 33%;
        background-color: #FFF;
        border-right: 1px solid #ccc;
        padding: 15px;
    }

    main {
        flex: 1 0 0%;
        padding: 15px;
    }
}

</style>
