<template>
    <div class="device-thumb">
        <div class="ar-1-1" ref="target">
            <img v-if="!thumbAvailable" src="../assets/no-connection.gif" />
            <img v-if="thumbAvailable" v-bind:src="thumbDataUrl" />
            <input type="checkbox" v-model="isSelected" @change="toggleSelect">
        </div>
        {{deviceId}} {{targetIsVisible}}
    </div>
</template>
<script>
import { ref } from 'vue'
import { useElementVisibility } from '@vueuse/core'

const apiBase = 'https://5lcpg07jce.execute-api.eu-west-2.amazonaws.com';

export default {
    name: 'DeviceThumb',
    props: ['hubBase', 'deviceId', 'selected'],
    setup() {
        const target = ref(null)
        const targetIsVisible = useElementVisibility(target)

        return {
            target,
            targetIsVisible,
        }
    },
    data: function() {
        return {
            isSelected: this.selected,
            thumbRefreshInterval : null,
            thumbAvailable : false,
            thumbInFlight : false,
            thumbDataUrl : ''
        }
    },
    methods: {
        toggleSelect : function() {
          this.$emit('toggle-select', {
            'device_id' : this.deviceId,
            'selected' : this.isSelected
          });
        },
        refreshThumb : async function() {
            if(this.targetIsVisible === false) {
                this.thumbAvailable = false;
                return;
            }
            if(this.thumbInFlight === false) {
                this.thumbInFlight = true;
                try {
                    const response = await fetch(apiBase + '/thumb/' + this.deviceId + '?cachebuster=' + (Math.random() * Number.MAX_SAFE_INTEGER));
                    if(response.ok) {
                      let blob = await response.blob();
                      let fileReader = new FileReader();
                      fileReader.onload = (e) => {
                        this.thumbDataUrl = e.target.result;
                        this.thumbInFlight = false;
                        this.thumbAvailable = true;
                      }
                      fileReader.readAsDataURL(blob);
                    }
                    else {
                      this.thumbAvailable = false;
                    }
                }
                catch(e) {
                  this.thumbAvailable = false;
                }
                this.thumbInFlight = false;
          }
        }
    },
    created : function () {
      this.thumbRefreshInterval = setInterval(this.refreshThumb, 1000);
    },
    beforeUnmount() {
      clearInterval(this.thumbRefreshInterval);
      this.thumbInFlight = false;
    }
}
</script>
<style lang="scss">
    .device-thumb {
        border: 1px solid #ccc;
        padding: 15px;

        .ar-1-1 {
            position: relative;
            width: 100%;
            padding-top: 100%;
            background-color: #ccc;

            img {
                position: absolute;
                top: 0px;
                left: 0px;
                width: 100%;
                height: 100%;
                object-position: center;
                object-fit: contain;
                padding: 15px;
            }

            input {
                position: absolute;
                top: 15px;
                left: 15px;
            }
        }
    }
</style>
