<template>
    <aside>
        <header class="d-flex align-items-center justify-content-center mb-5">
            <h1>Backdrop Hub</h1>
        </header>
        <nav>
            <div class="mb-3">
                <label class="form-label">Send Message</label>
                <div class="input-group input-group-sm">
                    <input type="text" class="form-control" v-model="message">
                    <span class="btn btn-primary input-group-text" @click="sendMessage">Send</span>
                </div>
            </div>
            <div class="mb-3">
                <label class="form-label">Open Website</label>
                <div class="input-group input-group-sm">
                    <input type="text" class="form-control" v-model="website">
                    <span class="btn btn-primary input-group-text" @click="openWebsite">Send</span>
                </div>
            </div>

            <div class="mb-3">
                <label class="form-label">Screens</label>
                <div class="btn-group d-flex">
                    <div class="btn btn-sm btn-outline-danger" @click="lockScreen">Lock</div>
                    <div class="btn btn-sm btn-outline-success" @click="unlockScreen">Unlock</div>
                </div>
            </div>

            <div class="mb-3">
                <label class="form-label">Browser</label>
                <div class="btn-group d-flex">
                    <div class="btn btn-sm btn-outline-danger" @click="disableWeb">Disable</div>
                    <div class="btn btn-sm btn-outline-success" @click="enableWeb">Enable</div>
                </div>
            </div>

            <div class="mb-3">
                <label class="form-label">Block Websites <small class="text-sm-end">(1 per line)</small></label>
                <textarea class="form-control" rows="5" v-model="blockList"></textarea>
                <div class="d-flex justify-content-end">
                    <span class="btn btn-sm btn-danger mt-2 me-2" @click="clearBlockList">Clear</span>
                    <span class="btn btn-sm btn-primary mt-2" @click="setBlockList">Apply</span>
                </div>
            </div>

            <div class="mb-3">
                <label class="form-label">Allow Websites</label>
                <textarea class="form-control" rows="5" v-model="allowList"></textarea>
                <div class="d-flex justify-content-end">
                    <span class="btn btn-sm btn-danger mt-2 me-2" @click="clearAllowList">Clear</span>
                    <span class="btn btn-sm btn-primary mt-2" @click="setAllowList">Apply</span>
                </div>
            </div>
        </nav>
    </aside>
</template>
<script>
export default {
    name: 'MultiDeviceControlPanel',
    data : function() {
        return {
            message : '',
            website : '',
            blockList : '',
            allowList : ''
        }
    },
    methods : {
        sendMessage : function() {
            this.$emit('send-message', {message : this.message});
        },
        openWebsite : function() {
          this.$emit('open-website', {url : this.website});
        },
        lockScreen : function() {
          this.$emit('lock-screen');
        },
        unlockScreen : function() {
          this.$emit('unlock-screen');
        },
        disableWeb : function() {
          this.$emit('disable-web');
        },
        enableWeb : function() {
          this.$emit('enable-web');
        },
        setBlockList : function() {
          this.$emit('set-block-list', { sites : this.blockList.split(/\r\n|\r|\n/g)});
        },
        clearBlockList : function() {
          this.$emit('clear-block-list');
        },
        setAllowList : function() {
          this.$emit('set-allow-list', { sites : this.allowList.split(/\r\n|\r|\n/g)});
        },
        clearAllowList : function () {
          this.$emit('clear-allow-list');
        }
    }
}
</script>
<style lang="scss">

aside {
    min-height: 100vh;
    flex: 0 0 auto;
    width: 33%;
    background-color: #FFF;
    border-right: 1px solid #ccc;
    padding:15px;
}
</style>
